import React from 'react';
import { Container } from '@mui/material';
import { GetCompanyInformation } from './Adapters/GetCompanyInformation';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import { useLocation } from 'react-router-dom';

const AboutUs = () => {
  const [CorpData, setCorpData] = React.useState({});
  const location = useLocation();
  const currentLocation = location.pathname;
  React.useEffect(() => {
    if (
      currentLocation === `/about` ||
      currentLocation === `/main/about`
    ) {
      GetCompanyInformation(false, setCorpData);
    }
  }, [currentLocation]);

  return (
    <React.Fragment>
      <Container
        sx={{
          color: themeColors.ColorPrimary,
        }}
      >
        {/* <h3>ما به بالینتان می آییم</h3> */}
        <br />
        <p style={{ textAlign: 'center' }}>
          {CorpData && CorpData.WAAbout}
        </p>
        <br />
      </Container>
    </React.Fragment>
  );
};

export default AboutUs;
